import PropTypes from 'prop-types'
import { Image, CloudinaryContext, Transformation } from 'cloudinary-react'

import './hero.scss'

const Hero = ( props ) => {
  const { imagePath } = props

  return (
    <section className={`hero`}>
      <CloudinaryContext cloudName={`tpierce36`}>
        <div className={`img-wrapper`}>
          <Image 
            publicId={imagePath}
            // responsive
            // width={`auto`}
            // height={`300`}
            // crop={`scale`}
          >
            <Transformation quality={`90`} fetchFormat={`auto`} gravity={`auto`} y={`2000`} crop={`scale`} width={`auto`} responsive />
          </Image>
        </div>
      </CloudinaryContext>
    </section>
  )
}

Hero.propTypes = {
  imagePath: PropTypes.string,
}

export default Hero